import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route
  } from "react-router-dom";
import MainPage from './MainPage/MainPage';
import ErrorPage from './ErrorPage/ErrorPage'
const App = () => {
    return (
        <div>
           <Router>
               <Switch>
                 <Route exact path="/">
                     <MainPage/>
                 </Route>
                 <Route path="/errorpage">
                    <ErrorPage/>
                 </Route>
               </Switch>
           </Router>
        </div>
    );
};

export default App;